.search-bar{
    display: flex;
    flex-direction: column;
    width: 300px;
    margin-bottom: 0.5rem;
}
#search button{
margin-left: 100px;
}
#search form{
    width: 300px;
    margin: 0 auto;
}
#search-results{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
#search-results a{
    margin-bottom: 1.5rem;
}